import * as types from './mutations-types';

const mutations = {
  [types.SET_FRIEND_LIST](state, friendlist) {
    state.friendlist = friendlist;
  },
  /* 修改tabs 状态 */
  [types.SET_GUI_STATUS](state, flag){
    state.GuideStatus = flag;
  },
  /* 记录主页的滚动数值 */
  [types.ADD_SCROLLY](state, data){
    state.scrollY.set(data.id, data.val);
  },
  [types.SET_TABINDEX](state, index) {
    state.tabactiveIndex = index;
  },
  [types.UPDATE_USER](state, user) {
    state.user = user;
  },
};

export default mutations;



