      <SwipeDelList>
        <swipebtn v-for="(item, index) in this.recommendList" @remove="remove" :userData="item" :key="index"></swipebtn>

      </SwipeDelList>
    </div>

    <div style="height:1.2rem;"></div>

    <div class="add-recommend">
      <div class="add-icon-wrapper" @click="addrecommend()">
          <img class="add-icon" src="./add@2x.png" alt="">
        </div>
    </div>



    <transition name="opacity">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script type="text/javascript">
  // import axios from 'axios';
  import { mapActions } from 'vuex';
  import SwipeDelList from '../../base/swipebtn/swipedellist';
  import swipebtn from '../../base/swipebtn/swipebtn';
  import header from '../wx-header/v-header';

  const data = require('../../../static/js/data.json');

  export default{
    data() {
      return {
        applyList: [],
        recommendList: []
      };
    },
    components: {
      'v-header': header,
      swipebtn,
      SwipeDelList,
    },
    methods: {
      remove() {
        console.log('aaa');
      },
      addrecommend() {
        console.log('添加');
      },
      _getRecommendData() {
/*         const url = '/api/1/recommend';
        axios.get(url).then((res) => {
          if (res.data.errno === 0) {
            this.applyList = res.data.data.ApplyData;
            this.recommendList = res.data.data.ListData;
          }
        }).catch((err) => {
          console.log(err);
        }); */
        this.applyList = data.recommendApplyData;
        this.recommendList = data.recommendListData;
      },
      ...mapActions([
        'setGuideStatus',
      ])
    },
    created() {
      this._getRecommendData();
      this.setGuideStatus(false);
    }
  };
</script>

