.recommend-container{
  height: 100%;
}

.preference-setting-container, .recommend-apply-container{
  width: 7.5rem;
  box-sizing: border-box;
}

.preference-title, .apply-title, .recommend-list-title{
  font-family:PingFangSC-Regular;
  height: .88rem;
  font-weight:normal;
  line-height: .88rem;
  font-size:.28rem;
  color:#313e46;
  letter-spacing:0.003rem;
  background:#f7f8fa;
  padding:0 .47rem;
}

.weui-cell_switch, .wallet-container{
  height:1.28rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding:0 .47rem;
  border-bottom: 0.01rem solid #ddd;
}

.weui-cell__bd{
  font-family:PingFangSC-Regular;
  font-size:.36rem;
  color:#758692;
  letter-spacing:0.003rem;
}

