<template>
  <div id="app">
    <keep-alive>
      <transition :name="routerName">
        <router-view></router-view>
      </transition>
    </keep-alive>
    <div v-show="guide" style="height:1.2rem"></div>
    <v-tabbar v-show="guide" class="tabbar" @toggleBar="togglebar"></v-tabbar>
  </div>

</template>

<script>
import { mapGetters } from 'vuex';
import tabbar from './base/tabbar/tabbar';

const routeText = ['/', 'Setting', 'Me'];

export default {
  data() {
    return {
      routerName: '',
      islogin: false,
    };
  },
  name: 'app',
  components: {
    'v-tabbar': tabbar
  },
  methods: {
    togglebar(index) {
      this.$router.push(routeText[index]);
    },
  },
  watch: {
    $route(to) {
      const toDepth = to.path.split('/');
      if (toDepth[1] === 'Me' || toDepth[0] === ''){
        this.routerName = ' ';
      } else {
        this.routerName = 'opacity';
      }
    }
  },
  computed: {
    ...mapGetters([
      'guide',
    ]),
  },
  mounted() {
    this.axios.get('/api/v1/users/check').then((res) => {
      // console.log(res);
      this.$store.dispatch('setUser', res.data.data);
      this.islogin = true;
    });

    if (!this.islogin) {
      this.axios.post('/api/v1/users/signUp', {
        username: 'Carson14',
        password: 'LZFtdqi2fwkuws6',
      }).then((res) => {
        this.$store.dispatch('setUser', res.data.data);
      }).catch((error) => {
        console.log(error);
      });
    }
  },
};
</script>

<style>
body{
  margin: 0;
  padding: 0;
}
.tabbar{
  background: #fff;
  position: fixed;
  bottom: 0;
  left:0;
}


.opacity-enter-active, .opacity-leave-active {
  transition: all .3s ease-in-out;
}

.opacity-enter, .opacity-leave-to {
  opacity: .3;
}
</style>



