<template>
    <div class="Righttou-Recommend">
      <div class="profile-header-container">
        <div class="profile-top">
          <h2 class="profile-title">对头推介</h2>
          <div class="middle-container">
            <img src="./right.png" alt="" class="fn-icon">
            <img class="user-image" src="./user@2x.png" alt="用户头像">
            <img src="./chat.png" alt="" class="fn-icon green-bg">
          </div>
        </div>
        <div class="profile-bottom">
          <div class="name">Linsk<span class="tag">认证牵线人</span></div>
          <div class="location-container">
            <i class="icon-location2"></i>
            <span>广州</span>
            <span>Guangzhou</span>
          </div>
          <div class="recommend-info">
            <span>266个推介</span>
            <span>66对牵线成功</span>
          </div>
        </div>
      </div>

      <div class="tab">
        <div class="tab-item" @click="toggleTab(1)">推介</div>
        <div class="tab-item" @click="toggleTab(4)">案例</div>
        <div class="line" :style="{'left': `calc(100vw / 6 * ${tabcontenIndex  + .5})`}"></div>
      </div>

      <div class="tab-content">
        <div class="tab-conent-item" v-show="tabcontenIndex === 1">
          <div class="index-content-container">
              <v-card v-for="(item, index) in data" :user="item" :key="index" @gotomatchmaker="gotomatchmaker"></v-card>
          </div>

        </div>
        <div class="tab-conent-item" v-show="tabcontenIndex === 4">

          <div class="example-container" >
            <div class="example-item" v-for="(item, index) in RightData" :key="index" :style="{height : item.success?4.66:3.66 +  'rem'}">
              <div class="status-image">
                <img class="user" src=" " alt="">
                <div class="placehold"></div>
                <img class="user" src=" " alt="">
              </div>
              <div class="status-text">
                <span>状态: <em :class="{ successText : item.success}">{{item.statusDesc}}</em></span>
                <span>进行时: {{item.time}}</span>
              </div>
              <div class="blessing-container" v-show="item.success">
                <div class="blessing-left">
                  <ul>
                    <li><img src=" " alt=""></li>
                    <li><img src=" " alt=""></li>
                    <li><img src=" " alt=""></li>
                    <li><img src=" " alt=""></li>
                    <li><img src=" " alt=""></li>
                  </ul>
                  <span class="blessing-text">+999祝福</span>
                </div>
                <div class="blessing-icon-container">
                  <img class="bg-icon" src="./search.png" alt="">
                  <img v-if="item.successStatus===1" class="icon-status-empty" src="./heart-fill.png" alt="">
                  <img v-else class="icon-status-empty"  src="./heart-empty.png" alt="">
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
</template>

<script type="text/javascript">
  import { mapActions } from 'vuex';
  import axios from 'axios';
  import card from '../../base/card/card';

  const RightData = [
    {
      success: false,
      statusDesc: '牵线中',
      time: '第五天'
    },
    {
      success: true,
      statusDesc: '恋爱',
      time: '第3年9个月',
      successStatus: 0,
    },
    {
      success: true,
      statusDesc: '恋爱',
      time: '第10年9个月',
      successStatus: 1,
    }
  ];

  export default{
    data() {
      return {
        data: [],
        RightData: [],
        tabcontenIndex: 1
      };
    },
    methods: {
      toggleTab(index) {
        this.tabcontenIndex = index;
      },
      gotomatchmaker(id) {
        this.$router.push(`/UserProfile?id=${id}`);
      },
      ...mapActions([
        'setGuideStatus',
      ])
    },
    mounted() {
      axios.get('api/1/discovery').then((res) => {
        this.data = res.data.data;
      });
      this.$nextTick(() => {
        this.RightData = RightData;
        window.scrollTo(0, 0);
      });
      this.setGuideStatus(false);
    },
    components: {
      'v-card': card
    }
  };
</script>

<style lang="css" scoped>

.Righttou-Recommend{
  width: 7.5rem;
  background-color: #ededed;
}

.profile-header-container{
  width:7.5rem;
}

.profile-top{
    position: relative;
    display: flex;
    justify-content: center;
    height: 2.8rem;
    background: #0f1b73;
  }

.profile-title{
    font-family:PingFangSC-Regular;
    margin-top: .2rem;
    font-size:.34rem;
    color:#fff;
    letter-spacing:0.041rem;
  }

.middle-container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 50%;
  left:0;
  padding: 0 .47rem;
  box-sizing: border-box;
  position: absolute;
  width: 7.5rem;
}

.fn-icon{
  width:.5rem;
  height:.42rem;
  background-color: #4990e2;
  padding: .2rem .4rem;
  border-radius: .5rem;
}

.green-bg{
  background-color: #60bf00;
}

.user-image{
    width: 2.58rem;
    height: 2.58rem;
    border-radius:100%;
  }

.profile-bottom{
    display: flex;
    box-sizing: border-box;
    width: 7.5rem;
    height: 3rem;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    justify-content: flex-end;
  }

.profile-bottom .name {
    font-family:PingFangSC-Medium;
    font-size: .34rem;
    color:#252d36;
    letter-spacing:0.0068rem;
}

.name .tag{
  font-family:PingFangSC-Thin;
  font-size:.14rem;
  margin-left: .3rem;
  color:#0f1b73;
  letter-spacing:0.0094rem;
  padding: .03rem .1rem;
  border: .01rem solid #0f1b73;
  border-radius: .5rem;
}

.profile-bottom .recommend-info, .profile-bottom .location-container{
 margin-top: .1rem;
  font-family:PingFangSC-Regular;
  font-size:.24rem;
  color:#b7c4cb;
  letter-spacing:0.01rem;
}

.tab{
  position: relative;
  display: flex;
  width: 7.5rem;
  height: .8rem;
  background-color: #fff;
}

.tab-item{
  font-family:PingFangSC-Regular;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size:.34rem;
  color:#252d36;
  letter-spacing:0.041rem;
  text-align:center;
  height:100%;
}

.line {
  position: absolute;
  bottom: 0;
  width: 1.42rem;
  border-bottom: 0.04rem solid #0f1b73;
  transition: left .1s ease-in-out;
  left: calc(100vw / 6 * 1.5);
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}

.tab-content{
  width: 7.5rem;
}

.index-content-container{
  margin-top: .2rem;
  width: 7.5rem;
}

.example-item{
  margin-top: .2rem;
  display: flex;
  width: 7.5rem;
  flex-direction: column;
  /*height:3.66rem;*/
  height: 4.66rem;
  justify-content: flex-end;
  align-items: center;
  background-color: #fff;
}

.status-image{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 .6rem;
  box-sizing: border-box;
}

.status-image .user{
  box-shadow:0 0.01rem 0.03rem 0 #bdc1c9;
  border-radius:.3rem;
  width:2rem;
  height:2rem;
}

.status-image .placehold {
  height: .4rem;
  width:1.2rem;
  margin-top: .5rem;
  border-bottom: .005rem solid #0091ea;
  border-top: .005rem solid #0091ea;
  margin: 0 .4rem;
}

.status-text{
  margin-top: .2rem;
  font-family:PingFangSC-Regular;
  font-size:.26rem;
  color:#252d36;
  letter-spacing:0.0093rem;
  margin-bottom: .5rem;
}
.status-text span{
  margin-left: 1rem;
}

.status-text .successText{
  color: #dd4356;
}

.blessing-container{
  position: relative;
  display: flex;
  box-sizing: border-box;
  justify-content: space-between;
  padding: 0 .2rem;
  align-items: center;
  border-top:.01rem solid rgba(183,195,203,0.50);
  height: 1rem;
  width: 7.5rem;
}

.blessing-left{
  display: flex;
  align-items:center;
}

.blessing-container ul{
  display: inline;
  font-size: 0;
}

.blessing-container li{
  position: relative;
  display: inline-block;
  width: .72rem;
  height: .72rem;
  margin-left: -.2rem;
}


.blessing-container li:nth-child(1){
  margin-left: 0;
}

.blessing-container li img{
  width: .72rem;
  height: .72rem;
  border-radius: 50%;
  background-color: #0f1b73;
}

.blessing-text{
  font-family:PingFangSC-Regular;
  font-size:.26rem;
  margin-left: .55rem;
  color:#b7c4cb;
  letter-spacing:0.0093rem;
}

.blessing-icon-container{
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width:.5rem;
  height:.5rem;
}

.bg-icon{
  position: absolute;
  left:0;
  top:0;
  width: .54rem;
  height: .5rem;
}

.icon-status-empty{
  position: absolute;
  width:.34rem;
  height: .28rem;
  margin-top: .02rem;
}

</style>



