<template>
  <div class="loading">
    <span>loading</span>
    <img class="loading-image" src="./loading@2x.png" alt="">
    <span>content</span>
  </div>
</template>

<script type="text/javascript">
    export default{};
</script>

<style lang="css" scoped>
.loading{
  display: flex;
  width: 7.5rem;
  height:.97rem;
  align-items: center;
}

.loading .loading-image{
  margin:0 .3rem;
  height: .47rem;
  width:.47rem;
  animation: loading .8s infinite linear;
  /*animation-direction:alternate;*/
}

.loading span {
  flex: 1;
  font-size:.28rem;
  color:#b7c5cb;
  letter-spacing:0.003rem;
}

.loading span:nth-child(1) {
  text-align: right;
}

@keyframes loading{
  0% {
    transform:rotate(0) ;
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>



