<template>
    <div class="card-container">
        <div class="content-item" >
          <div class="recommender-container">
            <img class="recommender-image" v-lazy="user.avatar" alt="" @click="gotomatchmaker(0)">
            <img class="link-icon" src="./like@2x.png" alt="">
            <div class="recommender-info-container">
              <span class="recommender-name">{{user.name}}</span>
              <div class="recommender-more">
                <span class="address">{{user.address}}</span>
                <span class="age">{{user.age}}岁</span>
              </div>
            </div>
            <ul class="tag-container">
              <li v-for="tag in user.tags">{{tag}}</li>
            </ul>
          </div>
          <div class="matchmaker-container">
            <div class="matchmaker-info">
              <img class="matchmaker-image" v-lazy="user.matchmaker.avatar" alt="">
              <span >{{user.matchmaker.time}}''</span>
            </div>
            <span @click="gotomatchmaker(0)" class="more">进一步了解Ta</span>
          </div>
        </div>
    </div>
</template>

<script type="text/javascript">
  export default{
    props: {
      user: {
        type: Object,
      }
    },
    methods: {
      gotomatchmaker(id) {
        this.$emit('gotomatchmaker', id);
      }
    }
  };
</script>

<style lang="css" scoped>
  /* 下面的内容 */

  .content-item{
    width: 7.5rem;
    margin-bottom: .3rem;
  }

  /* 推荐人*/
  .recommender-container{
    position: relative;
    width: 7.5rem;
    height:9rem;
  }

  .recommender-image{
    width: 7.5rem;
    height:9rem;
  }

  .link-icon{
    position: absolute;
    width:.5rem;
    height:.46rem;
    right:.23rem;
    top:.25rem;
  }

  .recommender-info-container{
    position: absolute;
    display: flex;
    flex-direction: column;
    bottom: 0.2rem;
    left: .3rem;
  }

  .recommender-name{
    font-size:.36rem;
    line-height: .36rem;
    color:#fff;
    letter-spacing:.0072rem;
    margin-bottom: .1rem;
  }

  .recommender-more{
    font-size:.28rem;
    line-height: .28rem;
    color:#fff;
  }

  .recommender-more span{
    margin-left: .1rem;
  }

  .recommender-more span:nth-child(1){
    margin-left: 0;
  }

  .tag-container{
    position: absolute;
    list-style: none;
    bottom:.2rem;
    right: .3rem;
    font-size:.28rem;
    line-height: .28rem;
  }
  .tag-container li{
    display: inline-block;
    color:#fff;
    letter-spacing:0.01rem;
    margin-left: .1rem;
  }

  /*媒人*/
  .matchmaker-container{
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    padding:.3rem;
    align-items: center;
    height: 1rem;
    width: 7.5rem;
    line-height: 0;
    background: #fff;
    box-shadow:0px 0px 5px #333
  }

  .matchmaker-info{
    display: flex;
  }

  .matchmaker-info span{
    margin: .1rem .2rem;
    position: relative;
    font-size: .24rem;
    color:#9b9b9b;
  }

  .matchmaker-image{
    height: .68rem;
    width: .68rem;
  }

  .chat-icon{
    width: .5rem;
    height: .56rem;
    background: #000;
  }


  .more {
    display: inline-block;
    width: 2rem;
    font-family:PingFangSC-Regular;
    font-size:.28rem;
    padding:.2rem;
    padding-right: 0;
    color:#9b9b9b;
    letter-spacing:0;
  }
</style>



