<template>
  <div class="index-container">
    <!--<v-header :title="`发现`" :isBack="false" :detail="false" bottomline></v-header>-->

    <div class="matchmaker-info-container" v-show="isUserProfile">
        <div class="matchmaker-info-left">
          <img src="static/image/matchmaker1@2x.png" alt="">
          <div class="matchmaker-info-wrapper">
            <div class="name">
              Linsk  <span class="tag">认证牵线人</span>
            </div>
            <div class="matchmaker-case-container">
              <span>266个推介</span> <span>66对牵线成功</span>
            </div>
          </div>
        </div>
        <div class="operation-container">
          <div class="operation-item">
            <img class="operation-icon" src="./right@2x.png" alt="">
            <span class="opration-text">加为好友</span>
          </div>
          <div class="operation-item">
            <img class="operation-icon" src="./Apply@2x.png" alt="">
            <span class="opration-text">申请挂牌</span>
          </div>
        </div>
    </div>

    <div class="placeholder"></div>

    <div class="index-content-container">
      <v-card v-for="item in data"  :key="item.name" :user="item" @gotomatchmaker="gotomatchmaker"></v-card>
    </div>

  </div>
</template>

<script type="text/javascript">
  import { mapActions, mapMutations, mapGetters } from 'vuex';
  // import axios from 'axios';
  import header from '../wx-header/v-header';
  import card from '../../base/card/card';

  const data = require('../../../static/js/data.json');

  export default{
    data() {
      return {
        data: [],
        isUserProfile: true
      };
    },
    components: {
      'v-header': header,
      'v-card': card,
    },
    methods: {
      gotomatchmaker(id) {
        this.$router.push(`/UserProfile?id=${id}`);
      },
      ...mapActions([
        'setGuideStatus',
        'setTabIndex'
      ]),
      ...mapMutations([
        'ADD_SCROLLY'
      ])
    },
    computed: {
      ...mapGetters([
        'scrollYArr'
      ])
    },
    mounted() {
      // axios.get('/api/1/discovery').then(res => {
      //   this.data = res.data.data;
      // });
      this.data = data.discoveryData;
      if (this.isUserProfile) {
        document.querySelector('.placeholder').style.height = '1.55rem';
      } else {
        document.querySelector('.placeholder').style.height = '1.04rem';
      }
      this.setGuideStatus(true);
      this.setTabIndex(0);

//      let scrollY = this.scrollYArr.get('Discovery') ? this.scrollYArr.get('Discovery') : 0;
//      this.$nextTick(() => {
//        console.log(`enter, ${scrollY}`);
//        setTimeout(() => {
//          window.scrollTo(0, scrollY);
//        }, 20);
//      });
    },
    /* eslint-disable no-unused-vars*/
    beforeRouteEnter(to, from, next) {
      next(vm => {
        let scrollY = vm.scrollYArr.get('Discovery') ? vm.scrollYArr.get('Discovery') : 0;
        console.log(`enter, ${scrollY}`);
        setTimeout(() => {
          window.scrollTo(0, scrollY);
        }, 20);
      });
    },
    /* eslint-disable no-unused-vars*/
    beforeRouteLeave(to, from, next) {
      let scrollTop = document.body.scrollTop;
//      console.log(scrollTop);
      this.ADD_SCROLLY({ id: 'Discovery', val: scrollTop });
      next();
    }
  };
</script>

<style lang="css" scoped>
.index-container{
  background-color: #ededed;
}


.matchmaker-info-container{
  position: fixed;
  display: flex;
  z-index: 2;
  /*margin-top: -.1rem;*/
  width: 7.5rem;
  height: 1.60rem;
  padding: .4rem .2rem;
  justify-content: space-between;
  box-sizing: border-box;
  background: #fff;
}

.matchmaker-info-left{
  display: flex;
  align-items: center;
}

.matchmaker-info-left img{
  box-shadow:0 2px 4px 0 rgba(0,0,0,0.20);
  width:.8rem;
  height:.8rem;
  border-radius:100%;
}



.matchmaker-info-wrapper{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: .3rem;
}

.matchmaker-info-wrapper .name {
  display: flex;
  align-items: center;
  font-family:PingFangSC-Regular;
  font-size:.34rem;
  color:#252d36;
  letter-spacing:0.0133rem;
  margin-bottom: .1rem;
}

.matchmaker-info-wrapper .name .tag{
  font-family:PingFangSC-Thin;
  font-size:.12rem;
  color:#0f1b73;
  line-height: .24rem;
  margin-left: .3rem;
  border-radius: .4rem;
  padding: .03rem .1rem;
  border: 1px solid #0f1b73;
  letter-spacing:0.0094rem;
}

.matchmaker-info-wrapper .matchmaker-case-container{
  font-family:PingFangSC-Regular;
  font-size:.24rem;
  color:#b7c4cb;
  letter-spacing:0.0094rem;
}

.operation-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.operation-item {
  margin-bottom: .25rem;
  line-height: 0;
}


.operation-icon{
  width:.2rem;
  height:.2rem;
}

.opration-text{
  font-family:PingFangSC-Regular;
  font-size:.24rem;
  color:#b7c4cb;
  letter-spacing:0.0094rem
}


.placeholder {
  width: 7.5rem;
  height:1.7rem;
}


/* 下面的内容 */
.index-content-container{
  width: 7.5rem;
}

.content-item{
  width: 7.5rem;
  margin-bottom: .3rem;
}

/* 推荐人*/
.recommender-container{
  position: relative;
  width: 7.5rem;
  height:9rem;
}

.recommender-image{
  width: 7.5rem;
  height:9rem;
}

.link-icon{
  position: absolute;
  width:.5rem;
  height:.46rem;
  right:.23rem;
  top:.25rem;
}

.recommender-info-container{
  position: absolute;
  display: flex;
  flex-direction: column;
  bottom: 0.2rem;
  left: .3rem;
}

.recommender-name{
  font-size:.36rem;
  line-height: .36rem;
  color:#fff;
  letter-spacing:.0072rem;
  margin-bottom: .1rem;
}

.recommender-more{
  font-size:.28rem;
  line-height: .28rem;
  color:#fff;
}

.recommender-more span{
  margin-left: .1rem;
}

.recommender-more span:nth-child(1){
  margin-left: 0;
}

.tag-container{
  position: absolute;
  list-style: none;
  bottom:.2rem;
  right: .3rem;
  font-size:.28rem;
  line-height: .28rem;
}
.tag-container li{
  display: inline-block;
  color:#fff;
  letter-spacing:0.01rem;
  margin-left: .1rem;
}

/*媒人*/
.matchmaker-container{
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  padding:.3rem;
  align-items: center;
  height: 1rem;
  width: 7.5rem;
  line-height: 0;
  background: #fff;
  box-shadow:0px 0px 5px #333
}

.matchmaker-info{
  display: flex;
}

.matchmaker-info span{
  margin: .1rem .2rem;
  position: relative;
  font-size: .24rem;
  color:#9b9b9b;
}

.matchmaker-image{
  height: .68rem;
  width: .68rem;
}

.chat-icon{
  width: .5rem;
  height: .56rem;
  background: #000;
}


.more {
  display: inline-block;
  width: 2rem;
  font-family:PingFangSC-Regular;
  font-size:.28rem;
  padding:.2rem;
  padding-right: 0;
  color:#9b9b9b;
  letter-spacing:0;
}

</style>



