<template>
  <div class="duitou-header" ref="header" :class="{bottomBoxShadow : bottomline}" >
    <div class="back-icon" @click="back" v-if="isBack">
      <img src="./left@2.0.png" alt="">
    </div>
    <h1 class="header-title">
      <slot name="title">
        {{title}}
      </slot>
    </h1>
    <div class="r-container" v-if="detail">
      <slot name="rightview">
        <img  class="header-icon" src="./detail@2x.png" alt="">
      </slot>
    </div>
  </div>
</template>

<script type="text/javascript">
  export default{
    props: {
      title: {
        type: String,
        default: '',
      },
      bottomline: {
        type: Boolean,
        default: true,
      },
      isBack: {
        type: Boolean,
        default: true,
      },
      detail: {
        type: Boolean,
        default: true,
      },
      fixed: {
        type: Boolean,
        default: true,
      },
    },
    methods: {
      back() {
        this.$emit('back');
      },
    },
    mounted() {
      if (this.fixed) {
        this.$refs.header.style.position = 'fixed';
      }
    },
  };
</script>

<style lang="css" scoped>

  .duitou-header {
    position: relative;
    z-index: 1;
    top:0;
    left:0;
    height: 1rem;
    width: 100%;
    line-height: 1rem;
    background: #fff;
  }

  .back-icon {
    display: flex;
    position: absolute;
    align-items: center;
    padding:0 .2rem;
    left: 0.3rem;
    height: 1rem;
  }

  .back-icon img {
    position: relative;
    height: 0.33rem;
    width: 0.19rem;
  }

  .header-title {
    color:#1c2429;
    font-size: 0.36rem;
    letter-spacing: 0.003rem;
    text-align: center;
  }

  .r-container {
    display: flex;
    align-items: center;
    position: absolute;
    height: 1rem;
    right:0.3rem;
    top: 0;
  }
  .header-icon {
    width:0.47rem;
    height: 0.11rem;
  }

  .bottomBoxShadow{
    /*border-bottom: 0.01rem solid #ddd;*/
    box-shadow:0 1px 3px 0 rgba(0,0,0,0.10);
  }


</style>



