<template>
  <div class="medium-container">

    <!--<v-header :title="`我的媒介`" bottomline :isBack="false" :detail="false"></v-header>-->

    <div class="gp-container">
      <span class="gp-title">挂牌开关</span>
      <span class="gp-state">挂牌中</span>
    </div>

    <div class=" weui-cell_switch">
      <div class="weui-cell__bd">暂停所有挂牌</div>
      <div class="weui-cell__ft">
        <input class="weui-switch" type="checkbox"/>
      </div>
    </div>

    <div class="gp-invite-container" v-if="inviteList.length > 0 ">
      <div class="gp-invite-title-container">
        <span class="invite-text">挂牌邀请</span>
        <span class="invite-num">{{inviteList.length}}</span>
      </div>

      <SwipeDelList class="invite-item" >
         <swipebtn v-for="(item, index) in inviteList" :userData="item" :key="index"></swipebtn>
      </SwipeDelList>
    </div>

    <div class="mygp-container" v-if="gpList.length > 0 ">
      <div class="mygp-title-container">
        <span class="invite-text">我的挂牌</span>
        <span class="invite-num">{{gpList.length}}</span>
      </div>

      <SwipeDelList class="invite-item" >
        <swipebtn v-for="(item, index) in gpList" :userData="item"
                  @remove="cancelgp(index)" :key="index"
                  @detail="showRecommendDetail(index)" ref="swipbtn"></swipebtn>
      </SwipeDelList>
    </div>

    <div class="date-wrapper">
      <span>19 Nov 2017</span>
      <span>Thursday</span>
    </div>
    <loading></loading>

  </div>
</template>

<script type="text/javascript">
  // import axios from 'axios';
  import { mapActions } from 'vuex';
  import SwipeDelList from '../../base/swipebtn/swipedellist';
  import swipebtn from '../../base/swipebtn/swipebtn';
  import header from '../wx-header/v-header';
  import loading from '../../base/loading/loading';

  const data = require('../../../static/js/data.json');

  export default{
    data() {
      return {
        inviteList: [],
        gpList: [],
      };
    },
    components: {
      'v-header': header,
      SwipeDelList,
      swipebtn,
      loading,
    },
    methods: {
      cancelgp(index) {
        /* 点击事件按钮回弹 */
        this.$refs.swipbtn[index].swipte_back();
        this.weui.actionSheet([
          {
            label: '暂停在此朋友挂牌',
            onClick() {
              console.log('暂停在此朋友挂牌');
            }
          },
        ], [
          {
            label: '取消',
            onClick() {
              console.log('取消');
            }
          }
        ], {
          className: 'custom-classname'
        });
      },
      _getmediumData() {
        // const url = 'api/1/medium';

        // axios.get(url).then((res) => {
        //   if (res.data.errno === 0) {
        //     this.inviteList = res.data.data.InviteData;
        //     this.gpList = res.data.data.gpData;
        //   }
        // });
        this.inviteList = data.mediumInviteData;
        this.gpList = data.mediumGpData;
      },
      showRecommendDetail(index) {
        this.$router.push(`/Referralsettings:${index}`);
      },
      ...mapActions([
        'setGuideStatus',
      ])
    },
    created() {
      this._getmediumData();
      this.setGuideStatus(false);
    },
  };
</script>

<style lang="css" scoped>
  .medium-container{
    z-index: 5;
    background-color: #fff;
  }

  .gp-container {
    display: flex;
    width: 7.5rem;
    height: 1rem;
    box-sizing: border-box;
    justify-content: space-between;
    padding: 0 0.2rem;
    background-color: #f7f8fa;
    line-height: 1rem;
  }

  .gp-title {
    font-size: 0.44rem;
    color: #1c2429;
  }

  .gp-state {
    text-align: right;
    font-size: 0.36rem;
    color: #0091ea;
  }

  .weui-cell_switch {
    display: flex;
    box-sizing: border-box;
    padding: 0 0.2rem;
    width: 7.5rem;
    height: 1.28rem;
    align-items: center;
    letter-spacing: 0.003rem;
  }

  .weui-cell__bd {
    color: #758692;
    font-size: 0.36rem;
  }

  .weui-switch:checked {
    border: 1px solid #0091ea;
    background-color: #0091ea;
  }

.gp-invite-title-container, .mygp-title-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    width: 7.5rem;
    height: 0.88rem;
    font-size: 0.28rem;
    padding: 0.48rem;
    color: #313e46;
    letter-spacing: 0.003rem;
    background-color: #f7f8fa;
  }

.date-wrapper{
  margin-top: .2rem;
  font-family:LucidaGrande;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size:.24rem;
  color:#313e46;
  box-sizing: border-box;
  padding:0 .4rem;
  letter-spacing:0.003rem;
}


</style>



