<template>
  <div class="me-container">

    <div class="user-container">
      <img  class="icon" src="./setting@2x.png" alt=""  @click="goto('Rcommend')" >
      <img  class="user-avatar" :src="user.avatar" alt="">
      <img  @click="goto('ReviseUserInfo')" class="icon" src="./change@2x.png" alt="">
    </div>

    <div class="user-name">{{ user.username }}</div>

    <div class="gp-info" @click="goto('medium')">
      挂牌中(233)
    </div>

    <button class="btn" @click="goto('Friends')">好友</button>
    <button class="btn">收藏</button>
    <button class="btn" @click="goto('BrandDealtWith')">待办 <span class="todo-count">99+</span></button>
    <button class="btn">历史访问</button>
    <button class="btn">访客列表</button>
  </div>
</template>

<script type="text/javascript">
  import { mapActions, mapGetters } from 'vuex';
  import header from '../wx-header/v-header';

  export default{
    components: {
      'v-header': header
    },
    methods: {
      goto(path) {
        this.$router.push(`/${path}`);
      },
      ...mapActions([
        'setGuideStatus',
        'setTabIndex'
      ])
    },
    computed: {
      ...mapGetters([
        'user',
      ])
    },
    mounted() {
      document.body.ontouchstart = function (){
      };
      this.setGuideStatus(true);
      this.setTabIndex(2);
    }
  };
</script>

<style lang="css" scoped>

.me-container{
  box-sizing: border-box;
  padding-top: 1.04rem;
  background: #eceef3;
  width: 7.5rem;
  height:calc(100vh - 1.2rem);
  overflow: auto;
}

.user-container{
  display: flex;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  height: 1.3rem;
  width:7.5rem;
  margin-top:.5rem;
  padding: 0 .4rem;
}

.user-avatar{
  border-radius: 50%;
  width: 1.3rem;
  height: 1.3rem;
}

.user-container .icon{
  padding: .5rem;
  width: .48rem;
  height: .48rem;
}

.user-name{
  width: 7.5rem;
  margin-top: .15rem;
  text-align: center;
  font-size:.4rem;
  color:#252d36;
  letter-spacing:.0157rem;
}

.gp-info{
  width:7.5rem;
  height:1.08rem;
  font-size:.26rem;
  color:#758692;
  letter-spacing:0.0011rem;
  line-height:1.08rem;
  text-align:center;
}

.btn{
  position: relative;
  display: block;
  width:7.5rem;
  padding: .4rem;
  font-size:.34rem;
  color:#252d36;
  letter-spacing:0.0014rem;
  text-align:center;
  outline: none;
  border: 0;
  background: transparent;
}

.btn:active{
  background: #fff;
}

.todo-count{
  position: absolute;
  font-family:PingFangSC-Regular;
  display: inline-block;
  width: .5rem;
  height: .5rem;
  border-radius: 100%;
  background: #ff3b30;
  color: #fff;
  font-size:.24rem;
  letter-spacing:-0.005rem;
  text-align:center;
  line-height: .5rem;
  transform: translate(50%, 0);
}

/*.btn:hover{*/
  /*background-color: #fff;*/
/*}*/
</style>



