<template>
  <div class="tabbar-container">
    <div class="tabbar-item" @click="toggleTab(0)">
        <img class="tabbar-icon" src="./discovery@2x.png" alt="" >
        <span :class="{active : activeIndex === 0 }">发现</span>
    </div>
    <div class="tabbar-item"  @click="toggleTab(1)">
        <img class="tabbar-icon" src="./setting@2x.png" alt="">
         <span :class="{active : activeIndex === 1 }">设置</span>
    </div>
    <div class="tabbar-item"  @click="toggleTab(2)">
        <img class="tabbar-icon" src="./me@2x.png" alt="">
        <span :class="{active : activeIndex === 2 }">我的</span>
    </div>
  </div>
</template>

<script type="text/javascript">
  export default{
    methods: {
      toggleTab(index) {
        this.$emit('toggleBar', index);
      }
    },
    computed: {
      activeIndex() {
        return this.$store.state.tabactiveIndex;
      }
    }
  };
</script>

<style lang="css" scoped>
.tabbar-container{
  display: flex;
  align-items: center;
  height: 1.2rem;
  width: 7.5rem;
}

.tabbar-item{
  display: flex;
  flex-direction: column;
  width: 33.3%;
  height:100%;
  justify-content: center;
  align-items: center;
}



.tabbar-icon{
  width:.40rem;
  height:.36rem;
  margin-bottom: .1rem;
}

.tabbar-item span{
  font-size: .24rem;
  color: #1c2429;
}



.tabbar-item .active {
  color: #f9d7a0;
}


</style>



