<template>
  <div class="friends-container">
    <!--<v-header class="friend-header" :fixed="false" :title="`好友`" @back="back" bottomline></v-header>-->

    <scroll :data="friendList" class="friendlist" ref="list">
      <SwipeDelList >

        <swipebtn v-for="(item ,index) in friendList" :userData="item" @remove="isFriend" :key="index"></swipebtn>

        <v-loading ></v-loading>
      </SwipeDelList>
    </scroll>

  </div>
</template>

<script type="text/javascript">
  import { mapActions } from 'vuex';
  // import axios from 'axios';
  import SwipeDelList from '../../base/swipebtn/swipedellist';
  import swipebtn from '../../base/swipebtn/swipebtn';
  import header from '../wx-header/v-header';
  import loading from '../../base/loading/loading';
  import scroll from '../../base/scroll/scroll';

  const data = require('../../../static/js/data.json');

  export default{
    data() {
      return {
        friendList: []
      };
    },
    components: {
      swipebtn,
      SwipeDelList,
      'v-header': header,
      'v-loading': loading,
      scroll,
    },
    methods: {
      isFriend(flag) {
        if (flag) {
          this.weui.actionSheet([
            {
              label: '解除好友关系',
              onClick() {
                console.log('解除好友关系');
              },
            }, {
              label: '解除好友关系并下架推介',
              onClick() {
                console.log('解除好友关系并下架推介');
              },
            }, {
              label: '解除好友关系并取消挂牌',
              onClick() {
                console.log('解除好友关系并取消挂牌');
              },
            },
          ], [
            {
              label: '取消',
              onClick() {
                console.log('取消');
              },
            },
          ], {
            className: 'action-sheet-container',
          });
        } else {
          this.weui.actionSheet([
            {
              label: '举报',
              onClick() {
                console.log('举报');
              },
            }, {
              label: '不再接受其好友申请',
              onClick() {
                console.log('不再接受其好友申请');
              },
            }
          ], [
            {
              label: '取消',
              onClick() {
                console.log('取消');
              },
            },
          ], {
            className: 'action-sheet-container',
          });
        }
      },
      _getfriend() {
        // const url = 'api/1/friend';
        // axios.get(url).then((res) => {
        //   if (res.data.errno === 0) {
        //     this.friendList = res.data.data;
        //   }
        // });
        this.friendList = data.friendData;
      },
      back() {
        this.$router.back();
      },
      ...mapActions([
        'setGuideStatus',
      ])
    },
    created() {
      this._getfriend();
    },
    mounted() {
      this.setGuideStatus(false);
    }
  };
</script>

<style lang="css" scoped>

  .friends-container{
    position: fixed;
    top:0;
    left:0;
    bottom:0;
    z-index: 5;
    width: 7.5rem;
  }

  .friendlist{
    height:100%;
    overflow: hidden;
    background: #fff;
  }




</style>



