<template>
  <div class="setting-container">
    <v-header :title="`设置`" bottomline :isBack="false" :detail="false"></v-header>

  </div>
</template>

<script type="text/javascript">
  import { mapActions } from 'vuex';
  import header from '../wx-header/v-header';

  export default{
    components: {
      'v-header': header,
    },
    methods: {
      ...mapActions([
        'setTabIndex',
      ])
    },
    mounted() {
      this.setTabIndex(1);
    }
  };
</script>

<style lang="css" scoped>

</style>



