<template lang="html">
  <div class="swipe_del_item" ref="swipe_btn" v-bind:style="{ 'line-height': h + 'px'}"  @touchstart="touchstart($event)" @touchmove="touchmove($event)" @touchend="touchend($event)">
    <slot name="body">
      <div>左滑可以删除</div>
    </slot>
    <div ref="delBtn" class="swipe_del_btn" v-bind:style="{ width: w + 'px', right: -r + 'px' }">
      <slot name="rightview">
        <div class="del_btn">删除</div>
      </slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SwipeDel',
  props: {
  },
  data() {
    return {
      w: 'auto',
      h: 'auto',
      r: 'auto',
    };
  },
  mounted() {
    this.$nextTick(() => {
      const w = this.$refs.delBtn.getBoundingClientRect().width;
      const h = this.$refs.delBtn.getBoundingClientRect().height;
      this.w = w;
      this.r = w;
      this.h = h;
    });
  },
  methods: {
    /* 用来找到父元素 */
    findParent(el) {
      if (el.className === 'swipe_del_item') {
        return el;
      }
      let tarEl = el.parentNode;

      while (tarEl.className !== 'swipe_del_item') {
        tarEl = tarEl.parentNode;
      }
      return tarEl;
    },
    touchstart(event) {
      const self = this;
      const obj = this.findParent(event.target.parentNode);
      if (obj.className === 'swipe_del_item') {
        self.initX = event.targetTouches[0].pageX;
        self.initY = event.targetTouches[0].pageY;
        self.objX = (obj.style.webkitTransform.replace(/translateX\(/g, '').replace(/px\)/g, '')) *
          1;
      }
    },
    touchmove(event) {
      const WIDTH = this.w;
      const initX = this.initX; // 触摸位置
      const initY = this.initY; // 触摸位置
      let moveX = this.moveX; // 滑动时的位置
      let moveY = this.moveY; // 滑动时的位置
      let X = 0; // 移动距离
      let Y = 0; // 移动距离
      const objX = this.objX || 0; // 目标对象位置
      const obj = this.findParent(event.target.parentNode);

      if (objX === 0) {
        obj.style.wibkitTtransition = 'all 0s';
        if (obj.className === 'swipe_del_item') {
          moveX = event.targetTouches[0].pageX;
          moveY = event.targetTouches[0].pageY;
          X = moveX - initX;
          Y = moveY - initY;
          self.moveX = moveX;
          self.moveY = moveY;
          if (Math.abs(X) <= Math.abs(Y)) {
            return;
          }
          if (X >= 0) {
            obj.style.webkitTransform = `translateX(${0}px)`;
          } else if (X < 0) {
            let l = Math.abs(X);
            obj.style.webkitTransform = `translateX(${-l}px)`;
            if (l > WIDTH) {
              l = WIDTH;
              obj.style.webkitTransform = `translateX(${-l}px)`;
            }
            event.preventDefault();
          }
        }
      } else if (objX < 0) {
        if (obj.className === 'swipe_del_item') {
          moveX = event.targetTouches[0].pageX;
          moveY = event.targetTouches[0].pageY;
          X = moveX - initX;
          Y = moveY - initY;
          self.moveX = moveX;
          self.moveY = moveY;
          if (Math.abs(X) <= Math.abs(Y)) {
            return;
          }
          if (X >= 0) {
            let r = -WIDTH + Math.abs(X);
            obj.style.webkitTransform = `translateX(${r}px)`;
            if (r > 0) {
              r = 0;
              obj.style.webkitTransform = `translateX(${r}px)`;
            }
            event.preventDefault();
          } else { // 向左滑动
            obj.style.webkitTransform = `translateX(${-WIDTH}px)`;
          }
        }
      }
    },
    touchend(event) {
      const WIDTH = this.w;
      const moveX = this.moveX; // 滑动时的位置
      const X = this.X || 0; // 移动距离
      let objX = this.objX || 0; // 目标对象位置
      const obj = this.findParent(event.target.parentNode);
      if (obj.className === 'swipe_del_item') {
        objX = (obj.style.webkitTransform.replace(/translateX\(/g, '').replace(/px\)/g, '')) * 1;
        if (objX > (-WIDTH / 2)) {
          objX = 0;
        } else {
          objX = -WIDTH;
        }
        obj.style.webkitTransition = 'all .5s';
        obj.style.webkitTransform = `translateX(${objX}px)`;
        self.objX = objX;
        self.moveX = moveX;
        self.X = X;
      }
    },
    swipe_back() {
      let obj = this.$refs.swipe_btn;
      obj.style.webkitTransition = 'all .5s';
      obj.style.webkitTransform = 'translateX(0px)';
    },
  },
};
</script>

<style lang="css">
  .swipe_del_item {
    border-bottom: 1px solid #fcfcfc;
    position: relative;
    padding: 0 12px;
    color: #666;
    background: #f2f2f2;
    -webkit-transform: translateX(0px);
  }
  .swipe_del_btn {
    overflow: hidden;
    position: absolute;
    top: 0;
    text-align: center;
    color: #fff;
  }
  .del_btn{
    background: #FF1744;
    height: 80px;
    width: 80px;
  }
</style>



