import Vue from 'vue';
import Router from 'vue-router';
import Mymedium from '@/components/my-medium/my-medium';
import Friends from '@/components/friends/friends';
import BrandDealtWith from '@/components/brand-dealt-with/brand-dealt-with';
import Rcommend from '@/components/recommend/recommend';
import Discovery from '@/components/discovery/discovery';
import Me from '@/components/me/me';
import Setting from '@/components/setting/setting';
import UserProfile from '@/components/user-profile/user-profile';
import ReviseUserInfo from '@/components/reviseUserInfo/reviseuserinfo';
import Referralsettings from '@/components/Referralsettings/Referralsettings';
import RighttouRecommend from '@/components/RighttouRecommend/RighttouRecommend';


Vue.use(Router);

export default new Router({
  routes: [
    { path: '/', name: 'home', component: Discovery },
    // { path: '/Discovery', component: Discovery },
    { path: '/medium', component: Mymedium },
    { path: '/Referralsettings:id', component: Referralsettings },
    { path: '/Rcommend', component: Rcommend },
    {
      path: '/Setting',
      component: Setting,
      redirect: '/medium',
    },
    { path: '/Me', component: Me },
    { path: '/ReviseUserInfo', component: ReviseUserInfo },
    { path: '/BrandDealtWith', component: BrandDealtWith },
    { path: '/Friends', component: Friends },
    { path: '/Discovery', component: Discovery },
    { path: '/UserProfile', component: UserProfile },
    { path: '/RighttouRecommend', component: RighttouRecommend },
    { path: '*', redirect: '/' },
  ],
});



