import * as types from './mutations-types';

export const setGuideStatus = function ({ commit, state }, flag) {
  commit(types.SET_GUI_STATUS, flag);
};

export const setfriendlist = function ({ commit, state }, list) {
  commit(types.SET_FRIEND_LIST, list);
};

export const setTabIndex = function ({ commit, state }, index) {
  commit(types.SET_TABINDEX, index);
};

export const setUser = function ({ commit, state }, user) {
  commit(types.UPDATE_USER, user);
};




