<template>
    <div class="swipebtn-wrapper">
      <SwipeDel ref="swipebtn" >
          <div slot="body" class="btn-info " >
            <div class="user-info">
              <div class="user-image-container " >
                <img  class="user-avatar" v-lazy= "userData.avatar" alt="">
                <span  v-bind:class="userData.isOnline ? 'circle-green' : 'circle-gray'"></span>
              </div>
              <div class="user-desc-container" >
                <h2 class="name" >{{userData.name}}</h2>
                <p class="user-desc">{{userData.desc}}</p>
              </div>
            </div>


            <img v-if="userData.showChecked" class="right-icon" src="./right@2x.png" alt="">

            <img v-else-if="userData.showOption"  class="detail-icon" src="./detail@2x.png" alt="detail" @click="detail()">

            <div v-else-if="userData.showRelationship" :class="userData.isFriend ? 'relationship_descr' : 'relationship_descr_fill'">
            同意
          </div>

          </div>

          <div slot="rightview" class="del-btn" @click="remove">
            <img class="remove-icon" src="./remove@2x.png" alt="">
          </div>
      </SwipeDel>
    </div>
</template>

<script type="text/javascript">
  import SwipeDel from './swipedel';

  export default{
    props: {
      userData: {
        type: Object
      },
    },
    components: {
      SwipeDel,
    },
    methods: {
      remove() {
        if (this.userData.isFriend !== 'undefined') {
          this.$emit('remove', this.userData.isFriend);
          return;
        }
        this.$emit('remove');
      },
      detail() {
        this.$emit('detail');
      },
      swipte_back() {
        this.$refs.swipebtn.swipe_back();
      }
    },
    mounted() {
    }
  };
</script>

<style lang="css" scoped>
  .swipe_del_item, .swipe_del_btn{
    padding:0;
  }
  .swipe_del_item{
    background-color: #fff;
  }

  .btn-info {
    display: flex;
    box-sizing: border-box;
    justify-content: space-between;
    padding: 0 0.47rem;
    align-items: center;
    height: 1.85rem;
    font-size: 0.36rem;
    position: relative;
  }

  .user-info{
    display: flex;

  }

  .user-image-container{
    display: flex;
    align-items: center;
    position: relative;
    height: 0.92rem;
    width: 0.92rem;
    border-radius: 50%;
  }

  .user-avatar{
    height: 0.92rem;
    width: 0.92rem;
  }

  .user-image-container .circle-green, .circle-gray{
    position: absolute;
    right:-0.05rem;
    bottom:0;
    border-radius: 50%;
    height: 0.28rem;
    width: 0.28rem;
    border: 0.04rem solid #fff;
  }

  .circle-green {
    background-color: #4bb906;
  }

  .circle-gray{
    background-color: #b7c4cb;
  }


  .user-desc-container{
    /*flex: 0 0 4.91rem;*/
    margin-left: .2rem;
    display: flex;
    flex-direction: column;
    line-height: 0.48rem;
    overflow: hidden;
  }

  .user-desc-container .name{
    font-size: 0.36rem;
    color:#1c2429;
    font-weight: normal;
  }

  .user-desc-container .user-desc{
    width:100%;
    font-size: 0.28rem;
    color:#0091ea;
    letter-spacing:0.003rem;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .user-desc.text-gray {
    color: #758692;
  }

  .right-icon{
    position: relative;
    height: 0.39rem;
    width: 0.39rem;
    top: 0.21rem;
  }

  .detail-icon{
    position: relative;
    top: 0.21rem;
    padding: .5rem 0rem;
    width:0.47rem;
    height: 0.11rem;
  }


  .relationship_descr, .relationship_descr_fill {
    display: flex;
    align-items: center;
    justify-content: center;
    height: .88rem;
    width: 2.15rem;
    color: #0091ea;
    font-size: .36rem;
    border-radius: 100px;
    border: 1px solid #0091ea;
  }

  .relationship_descr_fill {
    background-color: #0091ea;
    color: #fff;
  }


  .del-btn {
    /*height: 1.85rem;*/
    width: 1.87rem;
    font-size: 0.48rem;
    line-height: 1.85rem;
    background-color: #FF1744;
    text-align: center;
  }

  .remove-icon{
    height: 0.39rem;
    width: 0.39rem;
  }
</style>



